<template>
  <div :class="scrollClass" :style="scrollStyleCom" ref="scroll" @wheel="handleWheel">
    <div :class="prefixCls+'-content'" ref="content">
      <slot></slot>
    </div>
    <div
      :class="barClass"
      :style="barStyleCom"
      ref="bar"
      @mouseenter="handleBarMouseEnter"
      @mouseleave="handleBarMouseLeave"
    >
      <div :class="loadClass" :style="loadStyleCom" ref="load" @mousedown="handleLoadMouseDown"></div>
    </div>
  </div>
</template>
<script>
import { oneOf, preventDefault } from "utils/index.js";
import { debounce } from "utils/debounce.js";
const prefixCls = "vbestui-scroll";
export default {
  name: "BScroll",
  props: {
    height: {
      type: String,
      default: "",
    },
    theme: {
      validator(value) {
        return oneOf(value, ["light", "dark"]);
      },
      default: "light",
    },
    isDisable: {
      type: Boolean,
      default: false,
    },
    round: {
      type: Boolean,
      default: false,
    },
    barColor: {
      type: String,
      default: "",
    },
    barActiveColor: {
      type: String,
      default: "",
    },
    loadColor: {
      type: String,
      default: "",
    },
    loadActiveColor: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      prefixCls: prefixCls,
      barStyle: {},
      loadStyle: {},
      contentStyle: {},
      scrollStyle: {},
      loadHeight: 0,
      isScroll: false, //
      scrollY: 0, //Y
      loadDistanceY: 0, //load
      scrollPercent: 0, //
      offsetHeight: 0, //
      wheelDeltaY: 0, //Y
    };
  },
  computed: {
    scrollClass() {
      return [this.prefixCls];
    },
    scrollStyleCom() {
      return {
        height: this.height,
      };
    },
    barClass() {
      return [
        `${this.prefixCls + "-bar"}`,
        `${this.prefixCls + "-bar-" + this.theme}`,
      ];
    },
    barStyleCom() {
      return {
        background: this.barColor,
      };
    },
    loadClass() {
      return [
        `${this.prefixCls + "-bar-load"}`,
        `${this.prefixCls + "-bar-load-" + this.theme}`,
        this.isDisable ? `${this.prefixCls + "-bar-load-disable"}` : "",
      ];
    },
    loadStyleCom() {
      return {
        top: this.getLoadOffset + "px",
        height: this.loadHeight + "px",
        background: this.loadColor,
        borderRadius: this.round ? "8px" : "",
      };
    },
    getLoadOffset() {
      this.scrollPercent = Math.abs(this.wheelDeltaY) / this.offsetHeight;
      let offsetY = this.scrollPercent * this.scrollY;
      offsetY = offsetY < 0 ? 0 : offsetY;
      offsetY = offsetY > this.scrollY ? this.scrollY : offsetY;
      return offsetY;
    },
    /** */
    isSetBar() {
      return (
        this.barColor != "" &&
        this.barActiveColor != "" &&
        this.loadColor != "" &&
        this.loadActiveColor
      );
    },
  },
  methods: {
    /** */

    /**
     * @param y y
     * @param duration s
     */
    scrollTo(y, duration) {
      if (this.isDisable) return;
      this.wheelDeltaY = y;
      this.scrollRange();

      this.scrollPercent = this.wheelDeltaY / this.offsetHeight;
      this.setTransform(this.contentStyle, 0, this.wheelDeltaY, duration);
    },

    /**scroll */
    refresh() {
      /** */
      this.offsetHeight =
        this.$refs.content.offsetHeight - this.$refs.scroll.offsetHeight;
      /** /  */
      this.loadHeight =
        (this.$refs.scroll.offsetHeight / this.$refs.content.offsetHeight) *
        this.$refs.bar.offsetHeight;
      /** */
      this.scrollY = this.$refs.bar.clientHeight - this.loadHeight;
    },

    /** */
    handleWheel(e) {
      if (this.isDisable) return;
      this.offsetHeight =
        this.$refs.content.offsetHeight - this.$refs.scroll.offsetHeight;
      e = e || window.event;
      this.wheelDeltaY += e.wheelDeltaY || e.deltaY;
      /** */
      if (window.navigator.userAgent.indexOf("Firefox") !== -1) {
        this.wheelDeltaY += (e.wheelDeltaY || e.deltaY) * -50;
      }
      /** */
      this.scrollRange();
      /** */
      this.scrollPercent = Math.abs(this.wheelDeltaY / this.offsetHeight);
      this.setTransform(this.contentStyle, 0, this.wheelDeltaY, 0);
      /** */
      preventDefault();
      this.$emit("scroll", this.wheelDeltaY);
    },
    /** */
    scrollRange() {
      this.wheelDeltaY = this.wheelDeltaY > 0 ? 0 : this.wheelDeltaY;
      this.wheelDeltaY =
        this.wheelDeltaY < -this.offsetHeight
          ? -this.offsetHeight
          : this.wheelDeltaY;
    },
    /**load */
    handleLoadMouseDown(e) {
      if (this.isDisable) return;

      document.addEventListener("mousemove", this.handleLoadMouseMove);
      document.addEventListener("mouseup", this.handleLoadMouseUp);
      this.isScroll = true;
      this.enterBarStyle();

      /**load */
      let { y } = this.$refs.load.getBoundingClientRect();
      this.loadDistanceY = e.clientY - y;

      /** */
      document.body.style.userSelect = "none";
    },

    handleLoadMouseMove(e) {
      this.offsetHeight =
        this.$refs.content.offsetHeight - this.$refs.scroll.offsetHeight;
      /** */
      let { y } = this.$refs.scroll.getBoundingClientRect();
      let moveY = e.clientY - y - this.loadDistanceY;
      moveY = moveY > this.scrollY ? this.scrollY : moveY;
      moveY = moveY < 0 ? 0 : moveY;
      this.scrollPercent = Math.abs(moveY / this.scrollY);
      this.wheelDeltaY = -this.scrollPercent * this.offsetHeight;
      this.setTransform(
        this.contentStyle,
        0,
        -this.scrollPercent * this.offsetHeight,
        0
      );
      this.loadStyle.top = moveY + "px";

      this.$emit("scroll", this.wheelDeltaY);
    },
    handleLoadMouseUp(e) {
      this.isScroll = false;
      this.leaveBarStyle();
      document.removeEventListener("mousemove", this.handleLoadMouseMove);
      document.removeEventListener("mouseup", this.handleLoadMouseUp);

      document.body.style.userSelect = "initial";
    },
    /** */
    handleBarMouseEnter() {
      this.enterBarStyle();
    },
    /** */
    handleBarMouseLeave() {
      if (this.isScroll) return;
      this.leaveBarStyle();
    },
    setTransform(objStyle, x = 0, y = 0, duration = 0) {
      objStyle.transform = `translate(${x}px, ${y}px)`;
      objStyle["-ms-transform"] = `translate(${x}px, ${y}px)`; /* IE 9 */
      objStyle["-moz-transform"] = `translate(${x}px, ${y}px)`; /* Firefox */
      objStyle[
        "-webkit-transform"
      ] = `translate(${x}px, ${y}px)`; /* Safari  Chrome */
      objStyle["-o-transform"] = `translate(${x}px, ${y}px)`; /* Opera */
      objStyle.transition = `all ${duration}s`;
    },
    initScroll() {
      this.$nextTick().then(() => {
        this.barStyle = this.$refs.bar.style;
        this.loadStyle = this.$refs.load.style;
        this.contentStyle = this.$refs.content.style;
        this.scrollStyle = this.$refs.scroll.style;

        /** */
        this.refresh();
      });
    },
    enterBarStyle() {
      if (this.isDisable) return;
      this.theme == "light"
        ? this.setBarStyle("14px", "var(--bg-color)", "#a6a6a6")
        : this.setBarStyle("14px", "#1e1e1e", "#4f4f4f");
      if (!this.isSetBar || this.isSetBar) {
        this.setBarStyle(
          "14px",
          this.barActiveColor || this.barColor,
          this.loadActiveColor || this.loadColor
        );
      }
      this.loadStyle.borderRadius = this.round ? "14px" : "";
      this.scrollStyle.paddingRight = "14px";
    },
    leaveBarStyle() {
      this.theme == "light"
        ? this.setBarStyle("8px", "transparent", "var(--bg-color)")
        : this.setBarStyle("8px", "#1e1e1e", "#4f4f4f");
      if (!this.isSetBar || this.isSetBar) {
        this.setBarStyle("8px", this.barColor, this.loadColor);
      }
      this.loadStyle.borderRadius = this.round ? "8px" : "";
      this.scrollStyle.paddingRight = "8px";
    },
    /** */
    setBarStyle(width, barBgColor, loadBgColor) {
      this.barStyle.width = width;
      this.barStyle.backgroundColor = barBgColor;
      this.loadStyle.backgroundColor = loadBgColor;
    },
  },
  created() {
    this.initScroll();
    const _this = this;
    window.onresize = function () {
      _this.initScroll();
    };
  },
  watch: {
    scrollPercent() {
      if (this.scrollPercent < 0.05) {
        debounce(() => {
          this.$emit("pullingDown"); //
        })();
      } else if (this.scrollPercent > 0.95) {
        debounce(() => {
          this.$emit("pullingUp"); //
        })();
      }
    },
    /**height:height */
    height() {
      this.refresh();
    },
    /**bar */
    offsetHeight() {
      this.refresh();
    },
  },
};
</script>