<template>
  <span
    @click="change"
    :class="[prefixCls,
    value?'vbestui-switch-active':'',
    `vbestui-switch-${size}`,
    disabled?'vbestui-switch-disabled':'']"
    :style="{backgroundColor:value?trueColor:falseColor}"
  >
    <input type="hidden" :value="value" :name="name" />
    <span
      class="vbestui-switch-circle"
      :class="[value?`${prefixCls}-circle-active-${size}`:`${prefixCls}-circle-unactive-${size}`,
        size=='small'?`${prefixCls}-circle-small`:'']"
    ></span>
    <div v-if="value" :class="[size=='small'?'vbestui-open-small':'',`${prefixCls+'-open'}`]">
      <slot name="open">
        <i></i>
      </slot>
    </div>
    <div v-else :class="[size=='small'?'vbestui-close-small':'',`${prefixCls+'-close'}`]">
      <slot name="close">
        <i></i>
      </slot>
    </div>
  </span>
</template>
<script>
import { oneOf } from "utils/index.js";
const prefixCls = "vbestui-switch";
export default {
  name: "BSwitch",
  data() {
    return {
      prefixCls: prefixCls,
    };
  },
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    size: {
      validator(value) {
        return oneOf(value, ["large", "default", "small"]);
      },
      default: "default",
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    trueColor: {
      type: String,
      default: "",
    },
    falseColor: {
      type: String,
      default: "",
    },
    name: {
      type: String,
      default: "",
    },
  },
  methods: {
    change() {
      if (this.disabled) return;
      this.$emit("input", !this.value);
      this.$emit("toggle");
    },
  },
};
</script>