<template>
  <div :class="progressClass">
    <div :class="outClass" ref="progress" :style="outBgStyle" @click="handleClick">
      <div :class="innerClass" :style="innerBgStyle" ref="inner">
        <div
          :class="loadbarClass"
          v-show="isShow"
          v-if="showLoadbar"
          :style="loadbarStyle"
          @mousedown="handleMouseDown"
          @touchstart="handleTouStart"
          ref="loadbar"
        >
          <div :class="loadbarItemClass" v-if="showLoadbarItem" :style="loadbarItemStyle"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "BProgress",
  props: {
    /** */
    percent: {
      type: Number,
      default: 0,
    },
    allowClick: {
      type: Boolean,
      default: false,
    },
    allowDrag: {
      type: Boolean,
      default: false,
    },
    vertical: {
      type: Boolean,
      default: false,
    },
    proWidth: {
      type: String,
      default: "",
    },
    baseColor: {
      //progree 
      type: String,
      default: "",
    },
    strokeColor: {
      //
      type: [String, Array],
      default: "",
    },
    /** */
    isActive: {
      type: Boolean,
      default: false,
    },
    /** */
    loadbarColor: {
      type: String,
      default: "",
    },
    /** */
    loadbarItemColor: {
      type: String,
      default: "",
    },
    //
    showLoadbar: {
      type: Boolean,
      default: false,
    },
    //
    showLoadbarItem: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    isShow() {
      return this.percent * 1 != 0;
    },
    isShowPercent() {},
    /** */
    progressClass() {
      return [this.vertical ? "vbestui-progress-vertical" : "vbestui-progress"];
    },
    outClass() {
      return [
        this.vertical
          ? "vbestui-progress-outstyle-vertical"
          : "vbestui-progress-outstyle",
      ];
    },
    innerClass() {
      return [
        this.vertical
          ? "vbestui-progress-innerstyle-vertical"
          : "vbestui-progress-innerstyle",
        this.isActive
          ? this.vertical
            ? "vbestui-progress-vertical-active"
            : "vbestui-progress-active"
          : "",
      ];
    },
    loadbarClass() {
      return [
        this.vertical
          ? "vbestui-progress-loadbar-vertical"
          : "vbestui-progress-loadbar",
      ];
    },
    loadbarItemClass() {
      return ["vbestui-progress-loadbar-circle"];
    },
    /** */
    outBgStyle() {
      const style = {
        backgroundColor: `${this.baseColor}`,
        height: this.vertical ? "" : `${this.proWidth}`,
        width: this.vertical ? `${this.proWidth}` : "",
        borderRadius: this.proWidth ? `${this.proWidth}` : "", //border-radius
      };
      return style;
    },
    /** */
    innerBgStyle() {
      const style = {
        width: this.vertical ? `` : `${this.percent}%`,
        height: this.vertical ? `${this.percent}%` : "",
        background: this.vertical
          ? typeof this.strokeColor == "string"
            ? `${this.strokeColor}`
            : `linear-gradient(to top,${this.strokeColor[0]} 0%,${this.strokeColor[1]} 100%)`
          : typeof this.strokeColor == "string"
          ? `${this.strokeColor}`
          : `linear-gradient(to right,${this.strokeColor[0]} 0%,${this.strokeColor[1]} 100%)`,
        borderRadius: this.proWidth ? `${this.proWidth}` : "",
      };
      return style;
    },
    /** */
    loadbarStyle() {
      const style = {
        width: this.proWidth != "" ? `1.2em` : "",
        height: this.proWidth != "" ? `1.2em` : "",
        backgroundColor: `${this.loadbarColor}`,
      };
      return style;
    },
    /** */
    loadbarItemStyle() {
      const style = {
        /** */
        background: this.loadbarItemColor
          ? `${this.loadbarItemColor}`
          : typeof this.strokeColor == "string"
          ? `${this.strokeColor}`
          : `${this.strokeColor[0]}`,
      };
      return style;
    },
  },
  data() {
    return {
      isDrag: false,//
    };
  },
  methods: {
    handleClick(e) {
      /** */
      if (!this.allowClick) return false;
      if (this.isDrag) return;
      /** */
      const { clientX, clientY } = e;
      /** */
      let {x,bottom} = this.$refs.progress.getBoundingClientRect();

      let _percent;
      if (!this.vertical) {
        /** */
        let width = this.$refs.progress.offsetWidth;
        let _width = clientX - x;
        /** */
        _percent = _width / width* 100;
      } else {
        let verticalWidth = this.$refs.progress.offsetHeight;
        let _verticalWidth = bottom - clientY;
        _percent = _verticalWidth / verticalWidth * 100;
      }
      this.$emit("update:percent", _percent);
      this.$emit("click", _percent);
    },
    handleMouseDown(e) {
      /** */
      if (!this.allowDrag) return false;

      /** */
      this.isDrag = true;

      /** */
      document.body.style.userSelect = "none";
      /**transition */
      this.$refs.inner.style.transition = "none";

      this.$emit("update:percent", this.percent);
      this.$emit("dragbegin", this.percent);

      document.addEventListener("mousemove", this.handleMouseMove);
      document.addEventListener("mouseup", this.handleMouseUp);
    },
    handleMouseMove(e) {
      let { x, bottom } = this.$refs.progress.getBoundingClientRect();
      let _percent;
      /**
       * 
       */
      if (!this.vertical) {
        let moveX = e.clientX - x;
        /** */
        _percent = (moveX / this.$refs.progress.offsetWidth) * 100;
      } else {
        /**bottom */
        let moveY = bottom - e.clientY;
        _percent = (moveY / this.$refs.progress.offsetHeight) * 100;
      }

      /** */
      _percent = _percent < 0 ? 0 : _percent;
      _percent = _percent > 100 ? 100 : _percent;

      this.$emit("update:percent", _percent);
      this.$emit("dragging", _percent);
    },
    handleMouseUp(e) {
      this.isDrag = false;

      document.body.style.userSelect = "initial";
      this.$refs.inner.style.transition = "all .5s";

      this.$emit("update:percent", this.percent);
      this.$emit("dragend", this.percent);

      document.removeEventListener("mousemove", this.handleMouseMove);
      document.removeEventListener("mouseup", this.handleMouseUp);
    },

    handleTouStart(e) {
      /** */
      if (!this.allowDrag) return false;

      /** */
      this.isDrag = true;

      /** */
      document.body.style.userSelect = "none";
      /**transition */
      this.$refs.inner.style.transition = "none";

      this.$emit("update:percent", this.percent);
      this.$emit("dragbegin", this.percent);

      document.addEventListener("touchmove", this.handleTouchMove);
      document.addEventListener("touchend", this.handleTouchEnd);
    },
    handleTouchMove(e) {
      let { x, bottom } = this.$refs.progress.getBoundingClientRect();
      let _percent;
      /**
       * 
       */
      if (!this.vertical) {
        let moveX = e.clientX - x;
        /** */
        _percent = (moveX / this.$refs.progress.offsetWidth) * 100;
      } else {
        /**bottom */
        let moveY = bottom - e.clientY;
        _percent = (moveY / this.$refs.progress.offsetHeight) * 100;
      }

      /** */
      _percent = _percent < 0 ? 0 : _percent;
      _percent = _percent > 100 ? 100 : _percent;

      this.$emit("update:percent", _percent);
      this.$emit("dragging", _percent);
    },
    handleTouchEnd(e) {
      document.removeEventListener("touchmove", this.handleTouchMove);
      document.removeEventListener("touchend", this.handleTouchEnd);

      this.isDrag = false;

      document.body.style.userSelect = "initial";
      this.$refs.inner.style.transition = "all .5s";

      this.$emit("update:percent", this.percent);
      this.$emit("dragend", this.percent);
    },
  },
};
</script>